import axios from 'axios'
// TODO: axios instance를 활용하자(에러처리, 응답처리, 요청처리 편리)
import { useSnackbar } from 'notistack'
import { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import Slider from 'react-slick'

import { Box, Button, Container, type Theme, Typography, useTheme } from '@mui/material'

import ClubPreview, { type ClubPreviewProps } from 'src/components/ClubPreview'

import 'slick-carousel/slick/slick-theme.css'
import 'slick-carousel/slick/slick.css'

interface RecruitingClubType {
  theme: Theme
  isMqMatched: boolean
}

const RecruitingClubs: React.FC<RecruitingClubType> = ({ theme, isMqMatched }) => {
  const [clubs, setClubs] = useState<ClubPreviewProps[] | null>(null)
  const { enqueueSnackbar } = useSnackbar()

  const count = isMqMatched ? 20 : 8

  useEffect(() => {
    axios
      .get<ClubPreviewProps[]>(
        `${process.env.REACT_APP_APIHOST}/apply/clubs?page=${encodeURIComponent('1')}&count=${encodeURIComponent(
          `${count}`
        )}`
      )
      .then(res => {
        if (res.data?.length > 0) setClubs(res.data)
        else enqueueSnackbar('해당하는 동아리가 없습니다', { variant: 'warning' })
      })
      .catch(err => enqueueSnackbar(err.response.data?.message, { variant: 'error' }))
  }, [count])

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        padding: theme.spacing(2, isMqMatched ? 0 : 2)
      }}
    >
      <Container sx={{ padding: theme.spacing(1, 0, 5) }}>
        <Typography variant="h3" component="h2" gutterBottom sx={{ paddingBottom: 1, textAlign: 'center' }}>
          현재 모집 중인 동아리예요
        </Typography>
        {clubs && <ClubCarousel clubs={clubs} />}
      </Container>
      <Link to="search" style={{ textDecoration: 'none' }}>
        <Button variant="contained" size="large">
          더보기
        </Button>
      </Link>
    </Box>
  )
}

export default RecruitingClubs

interface ClubCarouselProps {
  clubs: ClubPreviewProps[]
}

const ClubCarousel: React.FC<ClubCarouselProps> = ({ clubs }) => {
  const {
    breakpoints: {
      values: { lg, sm }
    },
    spacing
  } = useTheme()

  const responsive = [
    {
      breakpoint: lg,
      settings: {
        slidesToShow: 3,
        slidesToScroll: 3
      }
    },
    {
      breakpoint: sm,
      settings: {
        slidesToShow: 1,
        slidesToScroll: 1
      }
    }
  ]

  return (
    <Slider dots infinite slidesToShow={4} slidesToScroll={4} autoplay responsive={responsive} arrows={false}>
      {clubs.map(club => (
        <Box key={club.id} sx={{ padding: spacing(0, 1) }}>
          <ClubPreview {...club} />
        </Box>
      ))}
    </Slider>
  )
}
